@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap');

body {
  margin: 0;
  font-family: 'Baskerville', serif;
}

.hero {
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding: 2rem;
}

.content {
  color: white;
  max-width: 40%;
}

.subtitle {
  font-size: 1.25rem;
  line-height: 1.8;
  text-align: justify;
}

.restaurant-name {
  font-family: 'Baskerville', serif;
  font-size: 14rem;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 4%;
  margin-top: 1%;
}

.about-us {
  padding: 10vh 2rem;
  background-color: #f9f9f9;
  color: #333;
}

.about-text {
  font-size: 1.25rem;
  line-height: 1.8;
  text-align: justify;
  max-width: 80%;
  margin: 0 auto;
}

.two-column {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10vh 2rem;
  padding-left: 10%;
  font-family: 'Baskerville', serif;
  text-align: left;
  color:#333;
}

.two-column .left {
  width: 50%;
}

.two-column .right {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.two-column img {
  width: 50%;
  height: auto;
  object-fit: cover;
}

.two-column h1 {
  font-weight: 400;
  font-size: 3rem;
  margin-bottom: 2rem;
}

.two-column p {
  font-size: 1.25rem;
  line-height: 1.8;
}


@media (max-width: 768px) {
  .hero {
    justify-content: center;
    align-items: center;
    height:50vh;
    width: auto;
  }

  .content {
    max-width: 100%;
    text-align: center;
  }

  .restaurant-name {
    font-size: 4rem;
    font-weight: 600;
    margin: 0;
  }

  .subtitle {
    display: none;
  }

  .about-us {
    margin: 0;
    padding: 8vh 2rem;
  }

  .about-text {
    font-size: 1rem;
  }

  .two-column {
    flex-direction: column;
    padding: 3rem 2rem;
  }

  .two-column .left, .two-column .right {
    width: 100%;
  }

  .two-column h2 {
    font-size: 2rem;
  }

  .two-column p {
    font-size: 1rem;
  }
}

